<template>
    <div>
        <top-nav title="抢购活动的商品列表"></top-nav>
        <div class="wyu-tips wyu-tips-warning">
            <el-row>
                <el-col :span="4">抢购活动ID：{{rushbuy_id}}</el-col>
                <el-col :span="4">活动名称：{{datas.rushbuy.name}}</el-col>
                <el-col :span="4">开始时间：{{datas.rushbuy.start_time}}</el-col>
                <el-col :span="4">结束时间：{{datas.rushbuy.end_time}}</el-col>
                <el-col :span="4">剩余时间：{{nowTime}}</el-col>
            </el-row>
            <el-row>
                <el-col></el-col>
            </el-row>
        </div>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form size="mini" v-model="searchForm" class="search-form" :inline="true">
                    <el-form-item label="商品ID">
                        <el-input v-model="searchForm.goods_id" placeholder="关键词"></el-input>
                    </el-form-item>

                    <el-form-item label="关键词">
                        <el-input v-model="searchForm.keywords" placeholder="关键词"></el-input>
                    </el-form-item>

                    <div style="padding-left:20px;">
                        <el-button size="mini" type="primary" @click="getList(1)">搜索</el-button>
                        <el-button size="mini" type="text" @click="searchForm.goods_id='',searchForm.keywords='',getList(1)">取消搜索</el-button>
                        <router-link :to="'/marketing/rushbuy/goodscreate/' + rushbuy_id">
                            <el-button size="mini" type="danger">添加关联商品</el-button>
                        </router-link>
                    </div>
                </el-form>
                <el-table :data="datas.list" style="width: 100%" size="small" @selection-change="handleSelectionChange">
                    <el-table-column type="selection" width="55"></el-table-column>

                    <el-table-column prop="goods_id" label="商品ID" width="60"></el-table-column>

                    <el-table-column prop="goods_name" label="商品名称" >
                        <template slot-scope="scope">
                            <div class="ellipsis-02">{{scope.row.goods_name}}</div>
                        </template>
                    </el-table-column>

                    <el-table-column label="商品缩略图" width="150">
                        <template slot-scope="scope">
                            <img width="60" :src="scope.row.photo"/>
                        </template>
                    </el-table-column>

                    <el-table-column label="规格"  width="160">
                        <template slot-scope="scope">
                            <p v-if="scope.row.spec.length == 1">无规格</p>
                            <p v-if="scope.row.spec.length > 1" class="ellipsis-02">
                                <span>{{scope.row.spec[0].key_name}} 等</span> <br/>
                                <b style="color: red">{{scope.row.spec.length}}</b> 种规格
                            </p>
                        </template>
                    </el-table-column>

                    <el-table-column label="最低抢购价"  width="160">
                        <template slot-scope="scope">
                            <p>注册会员:<b style="color: red">￥ {{scope.row.rushbuy_price}}</b></p>
                            <p>PLUS会员:<b style="color: red">￥ {{scope.row.vip_rushbuy_price}}</b></p>
                        </template>
                    </el-table-column>

                    <el-table-column prop="orderby" label="排序" width="80"></el-table-column>

                    <el-table-column label="操作日期/IP" width="200">
                        <template slot-scope="scope">
                            <p>日期：{{scope.row.add_time}}</p>
                            <p>IP：{{scope.row.add_ip}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column prop="goods_sn" label="是否上线" width="80">
                        <template slot-scope="scope">
                            <el-switch @change="setOnline(scope)" :value="scope.row.is_online" :inactive-value="0" :active-value="1" ></el-switch>
                        </template>
                    </el-table-column>

                    <el-table-column label="操作" width="200">
                        <template slot-scope="scope">
                            <el-col :span="8">
                                <router-link :to="'/marketing/rushbuy/goodsedit/' + scope.row.rushbuy_id + '/' + scope.row.rushbuy_goods_id">
                                    <el-button size="mini" type="text">编辑</el-button>
                                </router-link>
                            </el-col>

                            <el-col :span="8" style="text-align: center">
                                <el-popover placement="top" v-model="scope.row.is_delete">
                                    <p style="margin-bottom: 4px">确定删除吗？</p>
                                    <div style="margin: 0">
                                        <el-button :loading="loading" size="mini" type="text" @click="scope.row.is_delete = false">取消</el-button>
                                        <el-button :loading="loading" type="primary" size="mini" @click="del(scope)">确定</el-button>
                                    </div>
                                    <el-button type="text" size="mini" slot="reference">删除（释放产品）</el-button>
                                </el-popover>
                            </el-col>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="page">
                    <div class="page-mian">
                        <el-pagination @current-change="getList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import DateBetween from "@/components/DateBetween";

    export default {
        name: 'goodsAttrIndex',
        components: {TopNav,DateBetween},
        data() {
            return {
                //整体菊花
                loading: false,
                //列表数据
                datas: {
                    rushbuy:{},
                },
                rushbuy_id: 0,
                //搜索数据
                searchForm: {
                    rushbuy_id: '',
                    goods_id: '',
                    keywords: '',
                    adddate: [],
                    is_online: -999,
                },
                nowTime: '已结束',
                aa:0,
            }
        },

        created() {
            this.rushbuy_id = this.$route.params.id;
            this.getList();

        },
        methods: {
            countDown(times) {
                //单利
                let timer = null;
                if(times <= 0) return false;
                timer = setInterval(() =>  {
                    let day = 0,
                        hour = 0,
                        minute = 0,
                        second = 0// 时间默认值
                    if (times > 0) {
                        day = Math.floor(times / (60 * 60 * 24));
                        hour = Math.floor(times / (60 * 60)) - (day * 24);
                        minute = Math.floor(times / 60) - (day * 24 * 60) - (hour * 60);
                        second = Math.floor(times) - (day * 24 * 60 * 60) - (hour * 60 * 60) - (minute * 60);
                    }
                    if (day <= 9) day = '0' + day;
                    if (hour <= 9) hour = '0' + hour;
                    if (minute <= 9) minute = '0' + minute;
                    if (second <= 9) second = '0' + second;
                    this.nowTime  = `${day !== '00' ? `${day}天:` : ''}${hour}小时：${minute}分钟：${second}秒`;
                    times--;
                }, 1000);
                if (times <= 0) {
                    letTimes = "已结束";
                    clearInterval(timer);
                    return false;
                }

            },




            getList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.marketing.rushbuy/goodsindex", {
                    appToken: this.$store.getters.appToken,
                    rushbuy_id: this.$route.params.id,
                    keywords: this.searchForm.keywords,
                    goods_id: this.searchForm.goods_id,
                    bg_add_time: this.searchForm.adddate[0],
                    end_add_time: this.searchForm.adddate[1],
                    is_online: this.searchForm.is_online,
                    page: page,
                }).then(res => {
                    this.datas = res;
                    this.countDown(res.rushbuy.surplus_time);
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },


            handleSelectionChange(val) {
                let Ids = [];
                for (let i in val) {
                    Ids.push(i);
                }
                this.rushbuyIds = Ids;
            },



            setOnline(scope){
                this.loading = true;
                let is_online = 0;
                if (scope.row.is_online == 0) {
                    is_online = 1;
                }
                api.post("/shop/admin.marketing.rushbuy/goodsOnline", {
                    appToken: this.$store.getters.appToken,
                    rushbuy_goods_id: scope.row.rushbuy_goods_id,
                    is_online: is_online,
                }).then(res => {
                    this.getList();
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },


            del(scope) {
                this.loading = true;
                api.post("/shop/admin.marketing.rushbuy/goodsdel", {
                    appToken: this.$store.getters.appToken,
                    rushbuy_goods_id : scope.row.rushbuy_goods_id,
                }).then(res => {
                    this.getList();
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },
        }
    }
</script>

