<template>
    <div class="wyu-top">
        <div class="wyu-header">
            <div v-if="!title" class="wyu-tab">
                <ul>
                    <li v-for="(item,index) in showMenu" :class="index == select ? 'on' : '' " v-bind:key="index">
                        <router-link :to="item.link">{{item.title}}</router-link>
                    </li>
                </ul>
            </div>
            <div v-if="title" class="wyu-tab">
                <ul>
                    <li>{{title}}</li>
                </ul>
            </div>
            <div class="wyu-header-status">

                <div class="login-info">
                    <img src="@/assets/img/img_head_default.png"/>
                    <el-dropdown style="cursor: pointer">
                            <span class="el-dropdown-link">
                              {{$store.getters.appInfo.app_name}}  <i
                                    class="el-icon-arrow-down el-icon--right"></i>
                            </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item ><span @click="LogOut">退出登录</span></el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['mdl', 'select', 'title'],
        name: "TopNav",
        data: function () {
            return {
                showMenu: [],
                menu: {
                    'sms': [
                        {title: '发送短信', link: '/marketing/sms/send'},
                        //{title: '短信日志', link: '/marketing/sms/log'},
                        {title: '短信记录', link: '/marketing/sms/census'},
                    ],
                    // 'shopsetting': [
                    //     {title:'商城设置',link:'/setting/setting'},
                    //     {title:'库存预警设置',link:'/setting/shop/stock'},
                    //     {title:'快递鸟设置',link:'/setting/kdniao/index'},
                    //     {title:'关于我们',link:'/setting/about/index'},
                    //     {title:'积分设置',link:'/setting/integral/index'},
                    //     {title:'规则设置',link:'/setting/rule/index'},
                    // ],
                    'paymentSetting': [
                        {title:'微信小程序支付设置',link:'/setting/payment/weixin'},
                        {title:'支付宝小程序支付设置',link:'/setting/payment/ali'},
                        {title:'百度小程序支付设置',link:'/setting/payment/baidu'},
                    ],
                    'employ': [
                        {title:'员工管理',link:'/setting/employ'},
                        {title:'角色管理',link:'/setting/employ/role'},
                    ],
                    'agent': [
                        {title:'分销代理设置',link:'/agent/setting/index'},
                        {title:'顾问申请设置',link:'/agent/setting/apply'},
                        // {title:'发展下线设置',link:'/agent/setting/junior'},
                    ],
                    'coupon': [
                        {title:'优惠券管理',link:'/marketing/coupon/index'},
                        {title:'优惠券设置',link:'/marketing/coupon/setting'},
                        {title:'赠送优惠券',link:'/marketing/coupon/send'},
                    ],
                    'sucai' :[
                        {title:'素材管理',link:'/resource/index'},
                        {title:'素材分类 ',link:'/resource/category'},
                    ],
                    'wuliu' :[
                        {title:'运费模板',link:'/shipping/area/index/'},
                        {title:'快递公司 ',link:'/shipping/index'},
                    ],
                },
            };
        },
        created() {
            if (this.menu[this.mdl]) {
                this.showMenu = this.menu[this.mdl];
            } else {
                this.showMenu = [];
            }

        },
        methods:{
            LogOut(){
                var that  = this;
                this.$store.dispatch('LogOut').then(()=>{
                    that.$router.replace("/");
                });
            },
        },

    }
</script>

