<template>
    <div>
        <el-date-picker
                v-model="value"
                type="daterange"
                :picker-options="pickerOptions2"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                align="right"
                @change="selectDate"
        >
        </el-date-picker>
    </div>
</template>

<script>
    export default {
        name: "DateBetween",
        props:['date'],
        data() {
            return {
                pickerOptions2: {
                    shortcuts: [{
                        text: '最近七天',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }, {
                        text: '最近半个月',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 15);
                            picker.$emit('pick', [start, end]);
                        }
                    },
                        {
                            text: '最近一个月',
                            onClick(picker) {
                                const end = new Date();
                                const start = new Date();
                                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                                picker.$emit('pick', [start, end]);
                            }
                        },
                    ]
                },
                value:'',
            };
        },

        methods:{
            selectDate(val){
                let bgDate = 0;
                let endDate = 0;
                if(val){
                    bgDate = val[0].getFullYear() + '-' +  (val[0].getMonth()+ 1) + '-'  +  val[0].getDate();
                    endDate = val[1].getFullYear() + '-' +  (val[1].getMonth()+ 1) + '-'  +  val[1].getDate();
                }
                this.$emit("input",[bgDate,endDate]);
            },
        },
    }
</script>

